"use client";
import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import Image from "next/image";
import { getAnalytics } from "firebase/analytics";
import { getAuth, onAuthStateChanged } from "firebase/auth";
import { app, db } from "@/app/api/firebase";
import { collection, getDocs } from "firebase/firestore";
import Sidebar from "@/app/components/sidebar";
import Navbar from "@/app/components/navbar";
import ProjectCard from "@/app/components/ProjectCard";
import MeetingCard from "@/app/components/MeetingCard";


export default function Home() {
    const auth = getAuth(app);
    const analytics = getAnalytics(app);
    const [projects, setProjects] = useState([]);
    const [meetings, setMeetings] = useState([]);
    const [users, setUsers] = useState({});
    const [loading, setLoading] = useState(true);

    const fetchUsers = async () => {
        try {
            const usersSnapshot = await getDocs(collection(db, 'users'));
            const usersMap = {};
            usersSnapshot.forEach(doc => {
                usersMap[doc.id] = doc.data().name || doc.data().email;
            });
            setUsers(usersMap);
            return usersMap;
        } catch (error) {
            console.error('Error fetching users:', error);
            return {};
        }
    };

    const fetchData = async () => {
        try {
            const usersMap = await fetchUsers();
            
            // Fetch projects
            const projectsSnapshot = await getDocs(collection(db, 'projects'));
            const projectsList = projectsSnapshot.docs.map(doc => {
                const data = doc.data();
                return {
                    id: doc.id,
                    ...data,
                    ownerName: usersMap[data.owner] || data.owner,
                    participantsNames: data.participants?.map(id => usersMap[id] || id) || []
                };
            });
            setProjects(projectsList);

            // Fetch meetings
            const meetingsSnapshot = await getDocs(collection(db, 'meetings'));
            const meetingsList = meetingsSnapshot.docs.map(doc => {
                const data = doc.data();
                return {
                    id: doc.id,
                    ...data,
                    ownerName: usersMap[data.owner] || data.owner,
                    participantsNames: data.participants?.map(id => usersMap[id] || id) || []
                };
            });
            setMeetings(meetingsList);
        } catch (error) {
            console.error('Error fetching projects:', error);
        } finally {
            setLoading(false);
        }
    };
    useEffect(() => {
        fetchData();
    (async function fetchUsers() {
      try {
        const q = await getDocs(collection(db, 'users'));
        const u = [];
        q.forEach(d => u.push({ id: d.id, ...d.data() }));
        setUsersList(u);
      } catch (err) {
        console.error('Error fetching users for owner dropdown:', err);
      }
    })();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

    const formatDate = (date) => {
        return new Date(date).toLocaleDateString('en-GB', {
            day: '2-digit',
            month: '2-digit',
            year: 'numeric'
        });
    };

    const today = new Date().toLocaleDateString('en-GB', {
        day: '2-digit',
        month: '2-digit',
        year: 'numeric',
        weekday: 'long'
    });

    return (
        <div className="flex">
            <Sidebar/>
            <div className="flex-1">
                <Navbar/>
                <div className="p-6">
                    <div className="bg-gray-50 rounded-xl p-6 mb-6">
                        <h1 className="text-3xl font-bold mb-2">Hello {users[auth.currentUser?.uid] || 'User'}</h1>
                        <div className="text-gray-600">
                            <h2 className="text-xl">Today</h2>
                            <p className="text-2xl font-semibold">{today}</p>
                        </div>
                    </div>

                    <div className="mb-8">
                        <h2 className="text-xl font-semibold mb-4">Your Projects:</h2>
                        <div className="overflow-x-auto overflow-y-hidden">
                            <div className="flex gap-4 pb-2" style={{ width: 'max-content' }}>
                                {projects.map((project) => (
                                    <div key={project.id} style={{ minWidth: '400px', maxWidth: '400px' }}>
                                        <ProjectCard project={project} />
                                    </div>
                                ))}
                            </div>
                        </div>
                    </div>

                    <div className="bg-blue-50 rounded-xl p-6">
                        <h2 className="text-xl font-semibold mb-4">Meetings</h2>
                        <div className="overflow-x-auto overflow-y-hidden">
                            <div className="flex gap-4 pb-2" style={{ width: 'max-content' }}>
                                {meetings.map((meeting) => (
                                    <div key={meeting.id} style={{ minWidth: '300px', maxWidth: '300px' }}>
                                        <MeetingCard meeting={meeting} />
                                    </div>
                                ))}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
